#sectionRegistro{
  padding: 5% 5%;
  background: #0a0a3d;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.form{
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
  padding-right: 10%;
  border-right: 1px solid lightblue;
}

.formCadastro{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: baseline;
      align-items: baseline;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-direction: column;
      flex-direction: column;
}

.formCadastro label{
  width: 100%;
  margin: 1em 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.formCadastro label:nth-of-type(4){
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}

.formCadastro input[type="text"],
 .formCadastro input[type="email"]{
   padding: .5em;
 }

 .formCadastro input[type="checkbox"]{
   margin-right: 1em;
 }

 button{
   background-color: #0a0a3d;
   border-top-right-radius: 30px;
   border-top-left-radius: 30px;
   border-bottom-right-radius: 30px;
   border-bottom-left-radius: 30px;
   border: 1px solid lightblue;
   color: white;
   padding: 1em 1.5em;
   cursor: pointer;
 }

 .registroSocial{
   -ms-flex-preferred-size: 40%;
       flex-basis: 40%;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-direction: column;
       flex-direction: column;
   -ms-flex-align: center;
       align-items: center;
 }

 .registroSocial span{
   margin: 1em 0;
 }

 .registroSocial button{
   padding: 1em 3em;
 }



